@Component({
  selector: 'app-authenticating',
  templateUrl: './authenticating.component.html',
  styleUrls: ['./authenticating.component.css']
})
export class AuthenticatingComponent implements OnInit {
  @Input()
  oauthResponse$: Observable<OAuthResponse>;
  stringConverter: Operator<string, OAuthResponse>;
  counter = interval(100);
  constructor(
    private _podioService: PodioService,
    private _getTokenService: GetTokenService,
    private route: ActivatedRoute) {
      this.stringConverter = (s: string) => {
        // console.log(s._value);
        return new OAuthResponse('');
      };
      this.oauthResponse$ = this.route.fragment.lift(this.stringConverter);
      this.oauthResponse$.subscribe(this._getTokenService.OAuthObserver);
      this.oauthResponse$.subscribe(this._podioService.OAuthObserver);
    }

  ngOnInit() {
    console.log('init auth');
  }
}

