import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, Operator } from 'rxjs';
// import { jwtDecode } from 'jwt-decode';
import { environment } from 'src/environments/environment';
import { Jwt, JwtResponse } from 'src/model/saasafras/jwt';
import { SaasafrasService } from './saasafras.service';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private token: Jwt;
  private jwtDecoder = require('jwt-decode');
  public IsAuth$: Observable<boolean>;
  public tokenReader: Operator<JwtResponse, boolean>;

  constructor(private _router: Router, private saasafras: SaasafrasService) {
    this.tokenReader = (jwt: JwtResponse) => {
      return this.isTokenValid(this.decode(jwt.token));
    };
  }

  /**
   * this is used to clear anything that needs to be removed
   */
  private clear(): void {
    this.token = null;
    sessionStorage.clear();
  }

  setToken(tokenString: string) {
    const token = this.decode(tokenString);
    this.token = token;
    sessionStorage.setItem('token', tokenString);

    // use the service to set auth
    this.saasafras.SetJwt(tokenString);

    this._router.navigate(['/dashboard/solutions']);
    }

  isInRole(role: string): Observable<boolean> {
    const b = new Observable<boolean>((observer) => {
      const {next} = observer;
      try {
        const roles = this.token.scope.split(',');
        next(roles.includes(role));
      } catch (e) {
        next(false);
      }
    });
    return b;
  }

  isTokenValid(token: Jwt): boolean {
    const now = Date.now();
    return token.expiration > now
    && token.notBefore < now
    && token.issueDate < now
    && token.issuer === 'api.saassafras.com';
  }

  // this triggers a Podio OAuth sequence for the main user credentials
  // OAuth sequences for adding more Podio Users should not use this, as
  // they will need to redirect elsewhere
  login(): void {
    window.location.href = environment.podio.oauth.baseUrl + // create URL from parameters rather than hardcode
    '?response_type=token&client_id=' + environment.podio.oauth.clientId +
    '&redirect_uri=' + environment.podio.oauth.tokenRedirectUrl +
    '&state=12345';
  }

  // this triggers a Podio OAuth sequence for the service credentials
  // to be assigned at the solution or environment level
  loginServiceAccount(state: string): void {
    window.location.href = environment.podio.oauth.baseUrl + // create URL from parameters rather than hardcode
    '?response_type=token&client_id=' + environment.podio.oauth.clientId +
    '&redirect_uri=' + environment.podio.oauth.codeRedirectUrl +
    '&state=' + state;
  }

  /**
   * this is used to clear local storage and also the route to login
   */
  logout(): void {
    this.clear();
    this._router.navigate(['/auth']);
  }

  decode(tokenString: string): Jwt {
    const t = this.jwtDecoder(tokenString);
    // console.log(JSON.stringify(t));
    const j = new Jwt();
    // Issuer
    if (!t.iss) {
      throw new Error('Token has no issuer.');
    }
    j.issuer = t.iss;
    // Audience
    if (!t.aud) {
      throw new Error('Token has no audience.');
    }
    j.scope = t.aud;
    // Issue Date
    if (!t.iat) {
      throw new Error('Token has no issue date.');
    }
    j.issueDate = t.iat;
    // Expiration
    if (!t.exp) {
      throw new Error('Token has no expiration.');
    }
    j.expiration = t.exp;
    // Not Before
    if (!t.nbf) {
      throw new Error('Token has no start time.');
    }
    j.notBefore = t.nbf;
    // Subject
    if (!t.sub) {
      throw new Error('Token has no subject.');
    }
    j.accountId = t.sub;

    return j;
  }
}
