import { Component, OnInit } from '@angular/core';
import { SaasafrasService } from '../../services/saasafras.service';

@Component({
  selector: 'app-create-version-btn',
  templateUrl: './create-version-btn.component.html',
  styleUrls: ['./create-version-btn.component.css']
})
export class CreateVersionBtnComponent implements OnInit {

  constructor(private _saasafrasService:SaasafrasService) { }

  ngOnInit() {
  }
  async ButtonClicked()
  {
    document.getElementById("saveBtn").innerHTML="<i class=\"fa fa-refresh fa-spin\"></i>Creating";
    async function sleep(ms)
    {
      return new Promise(resolve => setTimeout(resolve, ms));
    }
    await sleep(2000);
    document.getElementById("saveBtn").innerHTML="Created!"
    await sleep(10);
    alert("A new version of "+document.getElementById("solutionVersion").innerHTML+" has been created!");
  }
  async CreateVersion()
  {
    this._saasafrasService.CreateVersion();
  }
}
