import { Component, OnInit } from '@angular/core';
import {DeployService} from '../../services/deploy.service';
@Component({
  selector: 'app-deploy-solution-btn',
  templateUrl: './deploy-solution-btn.component.html',
  styleUrls: ['./deploy-solution-btn.component.css']
})
export class DeploySolutionBtnComponent implements OnInit {

  constructor(private _deploymentService:DeployService) { }

  ngOnInit() {
  }
  DeploySolution()
  {
    document.getElementById("deploy").innerHTML="<i class=\"fa fa-refresh fa-spin\"></i>Deploying";
    var clientId=(<HTMLInputElement>document.getElementById("clientId")).value.toLowerCase();
    var envId=(<HTMLInputElement>document.getElementById("envId")).value.toLowerCase();
    var solutions=document.getElementsByClassName("check");
    var deployHtml=document.getElementById("deploy");
    var resultsHtml=document.getElementById("r");
    this._deploymentService.DeploySolution(clientId,envId,solutions,deployHtml,resultsHtml);
  }

}
