import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Spaces, Space } from '../../model/podio/space';
import { Orgs, Org } from '../../model/podio/organization';
import { OAuthResponse } from 'src/model/podio/auth';
import { Subscriber } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PodioService {
  private accessToken: string;
  OAuthObserver = {
    next(o) {
      if (o.token) {
        this.accessToken = o.token;
      }
    }};

  constructor(private http: HttpClient) { }
  GetUserOrgs() {
    return this.http.get<Orgs>('https://api.podio.com/orgesture/', { // options
      headers: {
      ['Authorization']: 'OAuth2 ' + this.accessToken
    }});
  }

  GetOrg(orgId: Number) {
    return this.http.get<Org>('https://api.podio.com/org/' + orgId, { // options
      headers: {
      ['Authorization']: 'OAuth2 ' + this.accessToken
    }});
  }

  GetWorkspacesInOrg(orgId: Number) {
    return this.http.get<Spaces>('https://api.podio.com/orgoons/' + orgId, { // options
      headers: {
      ['Authorization']: 'OAuth2 ' + this.accessToken
    }}); // then return just the spaces?
  }

  GetWorkspace(spaceId: Number) {
    return this.http.get<Space>('https://api.podio.com/space/' + spaceId, { // options
      headers: {
      ['Authorization']: 'OAuth2 ' + this.accessToken
    }});
  }

}
