import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { SaasafrasService } from '../../services/saasafras.service';
import { DeployService } from '../../services/deploy.service';

@Component({
  selector: 'app-solutions',
  templateUrl: './solutions.component.html',
  styleUrls: ['./solutions.component.css']
})
export class SolutionsComponent implements OnInit {

  constructor(private _authService: AuthService,
     private _solutionService: SaasafrasService,
     private _deploymentService: DeployService) { }

  ngOnInit() {
    const solList = document.getElementById('solutionsList');
    const selectOrg = document.getElementById('selectOrg');
    const urlHash = window.location.hash.substring(1);
    const split = urlHash.split('&');
    const accessToken = split[0].substring(split[0].indexOf('=') + 1);
    this._deploymentService.GetOrgs(accessToken, selectOrg);
  }
  GoBack() {
    location.reload();
  }
  GoToCreate() {
    document.getElementById('v').style.display = 'none';
    document.getElementById('areyousure').style.display = 'none';
    document.getElementById('f').style.display = 'none';
    document.getElementById('d').style.display = 'none';
    document.getElementById('a').style.display = 'block';
    document.getElementById('b').style.display = 'block';
    document.getElementById('second').style.display = 'none';
    document.getElementById('p').style.display = 'none';
    document.getElementById('e').style.display = 'none';
    document.getElementById('manage').style.display = 'none';
    document.getElementById('createnew').style.display = 'none';
  }

  SlideDiv() {
    document.getElementById('createnew').style.display = 'none';
    const solutions = document.getElementsByClassName('check');
    console.log(solutions.length);
    let checkedCount = 0;
    for (let i = 0; i < solutions.length; i++) {
      const solution = (<HTMLInputElement>(solutions[i]));
      if (solution.checked) {
        checkedCount += 1;
      }
    }
    if (document.getElementById('manage').innerHTML.includes('Manage Solution') && checkedCount > 0) {
      document.getElementById('manage').innerHTML = 'Cancel';
    } else {
      document.getElementById('manage').innerHTML = 'Manage Solution';
      document.getElementById('createnew').style.display = 'block';
    }
    if (checkedCount > 0) {
      document.getElementById('nosolution').style.display = 'none';
      const slider = document.getElementsByClassName('slider-parent')[0];
      if (slider.classList.contains('active')) {
        slider.classList.remove('active');
      } else {
        slider.classList.add('active');
      }
    } else {
      document.getElementById('nosolution').style.display = 'block';
    }
  }
}
