import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { OAuthResponse } from 'src/model/podio/auth';
import { JwtResponse } from 'src/model/saasafras/jwt';
import { HttpClient } from '@angular/common/http';
import { Observable, Subscriber } from 'rxjs';
import { AuthService } from './auth.service';
@Injectable({
  providedIn: 'root'
})
export class GetTokenService {
  OAuthObserver: Subscriber<OAuthResponse>;
  JwtResponse$: Observable<JwtResponse>;

  constructor(private http: HttpClient, private authService: AuthService) {
    this.OAuthObserver = new Subscriber<OAuthResponse>(
      (o: OAuthResponse) => {
        this.JwtResponse$ = this.GetToken(o);
        this.authService.IsAuth$ = this.JwtResponse$.lift<boolean>(this.authService.tokenReader);
      });
    }

  GetToken(response: OAuthResponse): Observable<JwtResponse> {
    console.log('entered GetToken()');

    return this.http.post<JwtResponse>(environment.saasafras.api.url + 'auth',
      response,
      { headers: {
        ['Content-Type']: 'application/json',
        ['Accept']: 'application/json'
      }});
  }
}

